<template>
  <div class="propertyOrderList" :class="{themeL:bindThemeL}">
    <!-- 头部分类切换 -->
    <div
      class="bar bar-header"
      style="z-index:999;box-shadow: 0 0 0.5px 0;"
      :style="{paddingTop:this.$stbHeight+'px'}"
    >
      <div class="commonHead" @click="onClickLeft()">
        <img class="leftArrow" :src="arrowLeftImg" />
      </div>
      <div class="title">
        <van-tabs
          v-model="typeSlideIndex"
          :swipe-threshold="6"
          color="#FB5768"
          @click="typeChanged"
          style="width:100%;"
        >
          <van-tab title="支付订单" :title-style="{fontSize: titleThemeL}"></van-tab>
          <van-tab title="房产账单" :title-style="{fontSize: titleThemeL}"></van-tab>
        </van-tabs>
      </div>
    </div>

    <div class="typeTabsPanel">
      <!-- 状态切换 -->
      <div class="has-header tabs typeTabs" v-if="myActiveSlide!=4">
        <div class="titles">
          <van-tabs
            v-model="statusSildeIndex"
            :swipe-threshold="4"
            color="#FB5768"
            style="width:100%;"
            @click="statusChanged"
          >
            <van-tab
              v-for="(tab,tabIndex) in tabList"
              :key="tabIndex"
              :title="tab.name"
              :title-style="{fontSize: titleThemeL}"
            ></van-tab>
          </van-tabs>
        </div>
        <div class="filter" @click="showFilterModal()">
          <van-icon v-if="!isShowModal" width="14" name="arrow-down" />
          <van-icon v-if="isShowModal" width="14" name="arrow-up" />
        </div>
      </div>

      <!-- 账单历史房间切换 -->
      <div
        class="has-header tabs"
        v-if="myActiveSlide==4"
        @click="selectArea()"
        style="background-color: #f5f5f7;font-weight: bolder;font-size: 16px;"
      >
        {{curRoomName}}
        <i class="icon ion-arrow-down-b" style="padding-left: 10px;"></i>
        <van-icon width="14" name="arrow-down" />
      </div>
    </div>
    <!-- 订单swipe-->
    <div class="orderSwiper">
      <div style=" height: 100%">
        <swiper id="target" ref="orderListSwipe" :options="swiperOption">
          <!-- 全部 -->
          <swiper-slide>
            <div class="swiper-item" :style="{'height': swiperItemH + 'px'}">
              <property-bill-none v-if="allList.length <= 0"></property-bill-none>
              <van-list
                v-if="allList.length > 0"
                v-model="allLoading"
                :finished="allFinished"
                finished-text="没有更多了"
                @load="listLoadMore"
              >
                <div
                  v-bind:class="item.billStatus==0?'recordItem': 'recordItemSmall'"
                  v-for="(item,orderindex) in allList"
                  :key="orderindex"
                  @touchstart="orderAdTouchStart()"
                  @touchmove="orderAdTouchMove()"
                  @touchend="orderAdTouchEnd(item)"
                >
                  <div class="top">
                    <div class="time">下单时间：{{item.createdTime}}</div>
                    <div class="status1" v-if="item.billStatus==0">待支付</div>
                    <div class="status2" v-if="item.billStatus==1">已支付</div>
                    <div class="status2" v-if="item.billStatus==2">已取消</div>
                  </div>
                  <div class="bottom" style="border-bottom:1px solid #f9f9f9">
                    <div class="pay">
                      <div>
                        <van-image width="26" :src="houseDefaultImg" />
                        <span>{{item.billType}}</span>
                      </div>
                      <div class="amount">￥{{item.payMoneyTotal}}</div>
                    </div>
                    <div class="goPay">
                      <div>{{item.areaName}}</div>
                    </div>
                    <div>
                      <div class="btn" v-if="item.billStatus == 0" @click="goOrderDetail(item)">
                        <div>去支付</div>
                      </div>
                    </div>
                  </div>
                </div>
              </van-list>
            </div>
          </swiper-slide>
          <!-- 待支付 -->
          <swiper-slide>
            <div class="swiper-item" :style="{'height': swiperItemH + 'px'}">
              <property-bill-none v-if="allList.length <= 0"></property-bill-none>
              <van-list
                v-if="allList.length > 0"
                v-model="allLoading"
                :finished="allFinished"
                finished-text="没有更多了"
                @load="listLoadMore"
              >
                <div
                  v-bind:class="item.billStatus==0?'recordItem': 'recordItemSmall'"
                  v-for="(item,orderindex) in allList"
                  :key="orderindex"
                  @touchstart="orderAdTouchStart()"
                  @touchmove="orderAdTouchMove()"
                  @touchend="orderAdTouchEnd(item)"
                >
                  <div class="top">
                    <div class="time">下单时间：{{item.createdTime}}</div>
                    <div class="status1" v-if="item.billStatus==0">待支付</div>
                    <div class="status2" v-if="item.billStatus==1">已支付</div>
                    <div class="status2" v-if="item.billStatus==2">已取消</div>
                  </div>
                  <div class="bottom" style="border-bottom:1px solid #f9f9f9">
                    <div class="pay">
                      <div>
                        <van-image width="26" :src="houseDefaultImg" />
                        <span>{{item.billType}}</span>
                      </div>
                      <div class="amount">￥{{item.payMoneyTotal}}</div>
                    </div>
                    <div class="goPay">
                      <div>{{item.areaName}}</div>
                    </div>
                    <div>
                      <div class="btn" v-if="item.billStatus == 0" @click="goOrderDetail(item)">
                        <div>去支付</div>
                      </div>
                    </div>
                  </div>
                </div>
              </van-list>
            </div>
          </swiper-slide>
          <!-- 已支付 -->
          <swiper-slide>
            <div class="swiper-item" :style="{'height': swiperItemH + 'px'}">
              <property-bill-none v-if="allList.length <= 0"></property-bill-none>
              <van-list
                v-if="allList.length > 0"
                v-model="allLoading"
                :finished="allFinished"
                finished-text="没有更多了"
                @load="listLoadMore"
              >
                <div
                  v-bind:class="item.billStatus==0?'recordItem': 'recordItemSmall'"
                  v-for="(item,orderindex) in allList"
                  :key="orderindex"
                  @touchstart="orderAdTouchStart()"
                  @touchmove="orderAdTouchMove()"
                  @touchend="orderAdTouchEnd(item)"
                >
                  <div class="top">
                    <div class="time">下单时间：{{item.createdTime}}</div>
                    <div class="status1" v-if="item.billStatus==0">待支付</div>
                    <div class="status2" v-if="item.billStatus==1">已支付</div>
                    <div class="status2" v-if="item.billStatus==2">已取消</div>
                  </div>
                  <div class="bottom" style="border-bottom:1px solid #f9f9f9">
                    <div class="pay">
                      <div>
                        <van-image width="26" :src="houseDefaultImg" />
                        <span>{{item.billType}}</span>
                      </div>
                      <div class="amount">￥{{item.payMoneyTotal}}</div>
                    </div>
                    <div class="goPay">
                      <div>{{item.areaName}}</div>
                    </div>
                    <div>
                      <div class="btn" v-if="item.billStatus == 0" @click="goOrderDetail(item)">
                        <div>去支付</div>
                      </div>
                    </div>
                  </div>
                </div>
              </van-list>
            </div>
          </swiper-slide>
          <!-- 已取消 -->
          <swiper-slide>
            <div class="swiper-item" :style="{'height': swiperItemH + 'px'}">
              <property-bill-none v-if="allList.length <= 0"></property-bill-none>
              <van-list
                v-if="allList.length > 0"
                v-model="allLoading"
                :finished="allFinished"
                finished-text="没有更多了"
                @load="listLoadMore"
              >
                <div
                  v-bind:class="item.billStatus==0?'recordItem': 'recordItemSmall'"
                  v-for="(item,orderindex) in allList"
                  :key="orderindex"
                  @touchstart="orderAdTouchStart()"
                  @touchmove="orderAdTouchMove()"
                  @touchend="orderAdTouchEnd(item)"
                >
                  <div class="top">
                    <div class="time">下单时间：{{item.createdTime}}</div>
                    <div class="status1" v-if="item.billStatus==0">待支付</div>
                    <div class="status2" v-if="item.billStatus==1">已支付</div>
                    <div class="status2" v-if="item.billStatus==2">已取消</div>
                  </div>
                  <div class="bottom" style="border-bottom:1px solid #f9f9f9">
                    <div class="pay">
                      <div>
                        <van-image width="26" :src="houseDefaultImg" />
                        <span>{{item.billType}}</span>
                      </div>
                      <div class="amount">￥{{item.payMoneyTotal}}</div>
                    </div>
                    <div class="goPay">
                      <div>{{item.areaName}}</div>
                    </div>
                    <div>
                      <div class="btn" v-if="item.billStatus == 0" @click="goOrderDetail(item)">
                        <div>去支付</div>
                      </div>
                    </div>
                  </div>
                </div>
              </van-list>
            </div>
          </swiper-slide>
          <!-- 账单历史 + 余额 -->
          <swiper-slide>
            <div class="swiper-item" :style="{'height': swiperItemH + 'px'}">
              <property-bill-none v-if="PrebillList.length<=0 && balance.length<=0"></property-bill-none>
              <!-- 预缴余额 -->
              <div class="card" style="margin-top:5px;" v-if="balance.length>0">
                <div class="item" style="padding:16px;">
                  <span style="font-weight:bolder;font-size: 18px;">预缴余额</span>
                  <span
                    class="item-note"
                    style="font-weight:bolder;color: #333333;font-size: 16px;"
                  >￥{{totalMoney | keepTwoNum}}</span>
                </div>
                <div
                  class="item"
                  style="padding:16px;"
                  v-for="(item,balanceindex) in balance"
                  :key="balanceindex"
                  :class="{itemThemeL:bindThemeL}"
                >
                  <span>{{item.chargeService}}</span>
                  <span
                    class="item-note"
                    :class="{itemNoteThemeL:bindThemeL}"
                    style="color: #333333;"
                  >￥{{item.totalMoney | keepTwoNum}}</span>
                </div>
              </div>
            </div>

            <property-bill-none v-if="PrebillList.length<=0 && balance.length>0"></property-bill-none>
            <!-- 账单历史 -->
            <div class="card" v-if="PrebillList.length>0">
              <div class="prebillTitle">账单历史</div>
              <van-list
                v-if="PrebillList.length > 0"
                v-model="allLoading"
                :finished="allFinished"
                finished-text="没有更多了"
                @load="recordLoadMore"
                :immediate-check="false"
              >
                <div class="list" style="padding:0px;">
                  <div
                    style="padding-left:0px;padding-right:0px;overflow:hidden;text-overflow: ellipsis; white-space: nowrap;"
                  >
                    <div v-for="(preBill,preindex) in PrebillList" :key="preindex">
                      <div
                        v-bind:class="preBill.isShow?'areaHead': 'areaHeadBack'"
                        @click="arrowDir(preBill)"
                      >
                        <div
                          class="areaName"
                          :class="{areaThemeL:bindThemeL}"
                        >{{preBill.payMonth.substring(0,4)}}年{{preBill.payMonth.substring(4,6)}}月</div>
                        <div
                          class="areaImg"
                          :class="{areaThemeL:bindThemeL}"
                          v-if="preBill.payFlag==1"
                        >
                          已缴费
                          <img
                            class="statusArrow"
                            :src="preBill.isShow?arrowDownImg:arrowRightImg"
                          />
                        </div>

                        <div
                          class="areaImg"
                          :class="{areaThemeL:bindThemeL}"
                          v-if="preBill.payFlag==0"
                          style="color: #FD666A"
                        >
                          待缴费
                          <img
                            class="statusArrow"
                            :src="preBill.isShow?arrowDownImg:arrowRightImg"
                          />
                        </div>
                      </div>
                      <div style="margin-top: 10px;z-index: 1;">
                        <div v-if="preBill.isShow">
                          <div
                            v-for="(detail,detailindex) in preBill.detailList"
                            :key="detailindex"
                            class="navText"
                          >
                            <div class="areaLine">{{detail.chargeItem}}</div>
                            <span
                              class="item-note"
                              style="width:100%;padding-right:16px;"
                            >￥{{detail.money| keepTwoNum}}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- <div class="card" ng-if="PrebillList.length>0"> -->
              </van-list>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>

    <div
      class="filterModal has-header"
      v-if="isShowModal"
      :style="{marginTop:(this.$stbHeight+88)+'px'}"
    >
      <div class="filterContent">
        <div class="title">按时间筛选</div>
        <div class="condition">
          <div
            v-for="(item,filterIndex) in filterList"
            :key="filterIndex"
            v-bind:class="item.code == myActiveCodition ? 'activeCondition' : ''"
            @click="chooseCondition(item.code)"
          >{{item.name}}</div>
        </div>
        <div class="btn">
          <div class="cancel" :class="{btnThemeL:bindThemeL}" @click="hideFilterModal()">取消</div>
          <div class="confirm" :class="{btnThemeL:bindThemeL}" @click="confirmFilter()">确定</div>
        </div>
      </div>
      <div style=" height: 100%;" @click="hideFilterModal()"></div>
    </div>
    <!-- 小区选择弹框 -->
    <van-popup
      v-model="showAreaSelectView"
      @click-overlay="selectAreaCancel"
      position="bottom"
      :style="{ height: '36%' }"
    >
      <!--<div class="areaselect-topbar">
        <div @click="selectAreaCancel">取消</div>
        <span @click="selectAreaOk">确定</span>
      </div>-->
      <van-picker
        show-toolbar
        :columns="areaNames"
        :default-index="selectAreaIndex"
        confirm-button-text="确定"
        @change="onChange"
        @cancel="selectAreaCancel"
        @confirm="selectAreaOk"
      />
    </van-popup>
  </div>
</template>

<script>
import {
  getAllOrderListService,
  roomBillListService,
  getRoomBalanceService
} from "../../../services/propertyBillService";

//swiper组件引入
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

import { getAreaListService } from "../../../services/homeService";
import propertyBillNone from "./propertyBillNone";
import { store, storage } from "../../../utils/storage";
export default {
  name: "propertyOrderList",
  data() {
    const self = this;
    return {
      tabIndex: 0,
      isShowModal: false, //筛选框是否显示
      tabList: [
        { code: "0", name: "全部", tabIndex: "0" },
        { code: "1", name: "待支付", tabIndex: "1" },
        { code: "2", name: "已支付", tabIndex: "2" },
        { code: "3", name: "已取消", tabIndex: "3" }
      ],
      allList: [],
      allFinished: false,
      allLoading: false,
      filterCode: "", //时间筛选
      myActiveSlide: 0, //当前显示的tab页
      preIndex: 0,
      PrebillList: [],
      balance: [],
      totalMoney: 0,
      curRoomName: this.$userInfo.ROOM_NAME, //当前房间
      showAreaSelectView: false, //房间选择器
      areaNames: [], //房间数组
      selectAreaIndex: 0, //房间筛选
      reqData: {
        //订单查询参数
        userCode: this.$userInfo.OWNER_CODE,
        orderStatus: "",
        pageNo: 1,
        pageSize: 10,
        orderTime: ""
      },
      roomBillData: {
        //账单历史查询参数
        roomCode: this.$userInfo.ROOM_CODE,
        pageNo: 1,
        pageSize: 10
      },
      myActiveCodition: "", // 选中的时间筛选条件
      filterList: [
        // 时间筛选
        { code: "", name: "全部" },
        { code: "1", name: "一个月" },
        { code: "6", name: "半年" },
        { code: "12", name: "一年" }
      ],
      statusSildeIndex: 0, //待支付、已支付类型切换tab
      typeSlideIndex: 0, //订单、账单类型切换tab
      isMoveing: false,
      houseDefaultImg: require("../../../assets/images/home/property/payment/icon_cost_default.png"),
      choicePointImg: require("../../../assets/images/home/property/payment/icon_choicePoint.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      arrowDownImg: require("../../../assets/images/home/property/payment/icon_arrow_down_black.png"),
      arrowLeftImg: require("../../../assets/images/common/nav_return_black.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme == 0 ? false : true,
      titleThemeL: this.$curTheme == 0 ? "14px" : "17px",

      swiperItemH: 580,
      swiperItemIndex: "0",
      swiperOption: {
        //首个居中
        centeredSlides: true,
        // 自动高度，让 siwper 的高度跟随 slide 的高度变化
        autoHeight: true,
        // 启动动态检查器
        observer: true,
        // 启动父类动态检查器
        observeParents: true,
        // 无限循环模式
        loop: false,
        on: {
          slideChangeTransitionStart: function() {
            self.onSwipeChange(this.realIndex);
            // console.log("===>>>>>> 当前index： " + this.realIndex);
          }
        }
      }
    };
  },
  components: {
    "property-bill-none": propertyBillNone,
    swiper,
    swiperSlide
  },
  computed: {
    //是否是第一次加载弹窗
    setOrderListPopupFlag() {
      return this.$store.state.property.orderListPopupFlag;
    }
  },
  watch: {
    /*记录当前页面有弹窗*/
    setOrderListPopupFlag(val) {
      console.log("监听被调用");
      if (val) {
        this.$store.dispatch("setOrderListPopupFlag", false);
        if (this.showAreaSelectView || this.isShowModal) {
          this.isShowModal = false;
          this.showAreaSelectView = false;
        } else this.onClickLeft();
      }
    }
  },
  mounted() {
    this.getAllOrderList();
    this.getArealist();

    this.$nextTick(() => {
      let otherH = this.$stbHeight + 44 + 44;
      this.swiperItemH = document.body.clientHeight - otherH;
    });
  },
  methods: {
    /* 查询订单方法 */
    getAllOrderList() {
      this.reqData.orderTime = this.filterCode;

      getAllOrderListService(this.reqData)
        .then(result => {
          //请求成功
          if (result.resultCode == 200) {
            if (result.data.billList.length < 10) {
              this.allFinished = true;
            }
            for (let i in result.data.billList) {
              this.allList.push(result.data.billList[i]);
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
          this.reqData.pageNo++;
          this.allLoading = false;
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.allLoading = false;
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //查询账单历史
    roomBillList() {
      roomBillListService(this.roomBillData)
        .then(result => {
          //请求成功
          if (result.resultCode == 200) {
            if (result.data.list.length < 10) this.allFinished = true;
            for (let i in result.data.list) {
              result.data.list[i].isShow = false;
              this.PrebillList.push(result.data.list[i]);
            }
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
          this.roomBillData.pageNo++;
          this.allLoading = false;
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.allLoading = false;
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //查询预缴余额
    getRoomBalance() {
      this.balance = [];
      getRoomBalanceService(this.roomBillData)
        .then(result => {
          //请求成功
          if (result.resultCode == 200) {
            this.totalMoney = result.data.totalMoney;
            this.balance = result.data.optionList;

            this.hide = true; //默认隐藏
            this.billList = [];
          } else {
            this.$toast(result.msg ? result.msg : "网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //swipe滑动事件
    onSwipeChange(index) {
      console.log("onSwipeChange");
      this.myActiveSlide = index;
      this.statusSildeIndex = index;
      this.PrebillList = [];
      this.allList = [];
      this.isShowModal = false;
      //判断订单or账单  显示不同页面
      if (index != 4) {
        this.typeSlideIndex = 0;
        this.reqData.pageNo = 1;
        this.allFinished = false;
        if (index == 0) {
          //全部
          this.reqData.orderStatus = "";
        } else {
          //其它状态
          this.reqData.orderStatus = "" + (index - 1);
        }
        this.getAllOrderList();
      } else {
        this.typeSlideIndex = 1;
        this.roomBillData.pageNo = 1;
        this.allFinished = false; //恢复变量
        this.roomBillList();
        this.getRoomBalance();
      }
      //列表滚动到顶部
      target.scrollIntoView();
    },
    //订单列表分页加载
    listLoadMore() {
      this.getAllOrderList();
    },
    //账单历史分页加载
    recordLoadMore() {
      this.roomBillList();
    },
    //箭头 展开与隐藏 小区下绑定的房间
    arrowDir(res) {
      res.isShow = !res.isShow;
    },
    // 月数筛选条件点击事件
    chooseCondition(index) {
      console.log(index);
      this.myActiveCodition = index;
      this.filterCode = index;
      if (this.myActiveSlide == "") {
        this.myActiveSlide = "0";
      }
    },
    // 筛选确认按钮
    confirmFilter() {
      this.hideFilterModal();
      this.allList = [];
      this.reqData.pageNo = 0;
      if (this.myActiveSlide == "") {
        this.myActiveSlide = "0";
      }
      this.getAllOrderList();
    },
    // 筛选点击事件
    showFilterModal() {
      this.isShowModal = !this.isShowModal;
    },
    // 筛选条件取消事件
    hideFilterModal() {
      this.isShowModal = false;
    },
    //跳转到详情页面
    goOrderDetail(item) {
      console.log("goOrderDetail");
      if (item.billType == "预缴费") {
        this.$router.push({
          name: "propertyPreOrderPayDetail",
          params: {
            billNo: item.billNo
          }
        });
      } else {
        this.$router.push({
          name: "propertyOrderPayDetail",
          params: {
            billCode: item.billNo,
            areaCode: item.areaCode
          }
        });
      }
    },
    /* 分类tab点击 */
    typeChanged(name, title) {
      if (name == 1) {
        // this.$refs.swipe.swipeTo(4);
        this.preIndex = this.myActiveSlide;
        this.$refs.orderListSwipe.swiper.slideTo(4);
      } else {
        // this.$refs.swipe.swipeTo(3);
        this.$refs.orderListSwipe.swiper.slideTo(this.preIndex);
      }
    },
    statusChanged(name, title) {
      // this.$refs.swipe.swipeTo(name);
      this.$refs.orderListSwipe.swiper.slideTo(name);
    },
    //点击小区选择器
    selectArea: function() {
      this.showAreaSelectView = true;
    },
    //选择小区取消按钮点击
    selectAreaCancel: function() {
      this.showAreaSelectView = false;
    },
    //选择小区确认按钮点击
    selectAreaOk: function(value, index) {
      this.selectAreaIndex = index;
      this.showAreaSelectView = false;
      console.log(JSON.stringify(this.selectAreaIndex));
      if (this.selectAreaIndex != undefined) {
        this.roomBillData.roomCode = this.areas[this.selectAreaIndex].ROOM_CODE;
        this.curRoomName = this.areaNames[this.selectAreaIndex];
        this.roomBillData.pageNo = 1;
        this.PrebillList = [];
        this.roomBillList();
        this.getRoomBalance();
      }
    },
    //小区选择器当前位置
    onChange: function(picker, value, index) {
      this.selectAreaIndex = index;
    },
    //获取小区列表
    getArealist: function() {
      let reqData = {
        OWNER_CODE: this.$userInfo.OWNER_CODE,
        CITY_CODE: this.$userInfo.CITY_CODE,
        CITY_NAME: this.$userInfo.CITY_NAME
      };
      getAreaListService(reqData)
        .then(noteData => {
          if (noteData.length > 0) {
            this.areas = noteData;
            this.areaNames = [];
            let index = 0;
            noteData.forEach(element => {
              this.areaNames.push(element.showAllName);
              if (
                this.$userInfo.ROOM_CODE != undefined &&
                this.$userInfo.ROOM_CODE === element.ROOM_CODE
              ) {
                this.selectAreaIndex = index;
                this.curRoomName = element.showAllName;
              }
              index++;
            });
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onClickLeft() {
      if (this.$route.params.flag == 10) {
        //需要跳转到我家首页
        this.$router.push({ path: "/homePage" });
      } else {
        this.$router.go(-1); //返回上一层
      }
    },

    orderAdTouchStart: function() {
      this.isMoveing = false;
    },
    orderAdTouchMove: function() {
      this.isMoveing = true;
    },
    orderAdTouchEnd: function(item) {
      if (this.isMoveing) {
      } else {
        this.goOrderDetail(item);
      }
    }
  }
};
</script>

